.filters {
  position: relative;
  display: flex;
  justify-content: flex-start;
  width: 100%;
  align-items: center;
  background: $greyLight;
  border-radius: 28px;
  min-height: 161px;
  padding: 60px 68px;
  padding-bottom: 28px;
  margin-bottom: 40px;
  flex-flow: row wrap;
  @include max(sm) {
    justify-content: center;
    padding: 34px 27px;
    padding-bottom: 19px;
  }
  &__btn {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top:18px;
    right: 18px;
    width: 30px;
    height: 30px;
    border-radius: 5px;
    transition: all .3s;
    @include max(sm) {
      top: 5px;
      right: 10px;
    }
    &:hover {
      transition: all .3s;
      background: $grey;
    }
  }
  .filter {
    position: relative;
    display: flex;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    align-items: center;
    background: transparent; 
    border: 1px solid $mainDark;
    box-sizing: border-box;
    color: $mainDark;
    min-height: 45px;
    max-width: 173px;
    margin-right: 28px;
    margin-bottom: 28px;
    padding-left: 16px;
    padding-right: 16px;
    font-size: 12px;
    font-style: normal;
    font-weight: 550;
    line-height: 135%; 
    text-transform: uppercase;
    border-radius: 5px;
    transition: all .3s;
    @include max(xs) {
      justify-content: center;
      margin-right: 7.5px;
      margin-left: 7.5px;
      width: calc(50% - 15px);
      max-width: auto;
      margin-bottom: 15px;
      padding-left: 5px;
      padding-right: 5px;
      font-size: 11px;
    }
    // @include max(xs) {
    //   &:nth-child(2n - 1) {
    //     margin-right: 0;
    //   }
    // }
    &:last-child {
      margin-right: 0;
      @include max(sm) {
        margin-right: 7.5px;
        margin-bottom: 15px;
        
      }
    }
    &:hover {
      transition: all .3s;
      background: $mainDark;
      border: 1px solid transparent;
      color: $white;
    }   
  }
}
