.selection {
  &__section {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    width: 100%;
    margin-bottom: 94px;
    @include max(mdHeader) {
      flex-flow: column nowrap;
    }
    @include max(sm) {
      margin-bottom: 56px;
    }
  }
  &__bottom {
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
  }
  &__item {
    position: relative;
    display: flex;
    width: calc(50% - 15px);
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    background: $greyLight;
    border-radius: 28px;
    padding: 48px;
    padding-bottom: 75px;
    @include max(mdHeader) {
      width: calc(100% - 0px);
      margin-bottom: 35px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    @include max(sm) {
      padding: 32px;
      padding-bottom: 64px;
      margin-bottom: 25px;
    }
    &__title {
      position: relative;
      width: 100%;
      display: block;
      text-align: center;
      margin-bottom: 16px;
      @include max(xs) { 
        margin-bottom: 11px;
      }
    }
    &__text {
      position: relative;
      width: 100%;
      display: block;
      text-align: center;
      max-width: 400px; 
      @include max(xs) {
        max-width: 290px; 
      }    
    }
    &__picture {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      max-width: 400px;
      margin-top: -60px;
      @include max(xs) { 
        margin-top: -20px;
      }
      &:before {
        position: relative;
        display: block;
        width: 100%;
        content: '';
        padding-top: 100%;
      }
      img, picture {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0px;
        display: block;
        object-fit: cover;
        object-position: center center;
      }
    }
    &__btn {
      position: absolute;
      bottom: 48px;
      left: 0;
      right: 0;
      margin-left: auto;
      margin-right: auto;
      @include max(sm) { 
        bottom: 32px;
      }
    }
  }
}